import React from 'react';
import Carousel from 'react-material-ui-carousel';
import { Paper, Grid, Box, makeStyles, Typography, Container } from '@material-ui/core';
import CarousalCard from '../../Components/Cards/CarouselCard';

const useStyles = makeStyles({
});


const Recents = (props) => {
    var items = [
        {
            name: "Random Name #1",
            description: "Probably the most random thing you have ever seen!"
        },
        {
            name: "Random Name #2",
            description: "Hello World!"
        }
    ]
    const classes = useStyles();

    return (
        <Box className={classes.root}>
            <Grid Container direction="column">
                <Grid item container>
                    <Grid item sm={2}></Grid>
                    <Grid item xs={12} sm={6}>
                        <Carousel animation='slide' navButtonsAlwaysVisible={false} indicators={false}>
                            {
                                items.map((item, i) => <Item key={i} item={item} />)
                            }
                        </Carousel>
                    </Grid>
                    <Grid item xs={false} sm={4} >
                        <Container>
                            <Typography>
                                space for ad
                       </Typography>
                        </Container>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    )
}

function Item(props) {
    return (
        <Paper>
            <CarousalCard />
        </Paper>
    )
}


export default Recents;