import React from 'react';
import NoSsr from '@material-ui/core/NoSsr';
import GoogleFontLoader from 'react-google-font-loader';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import { CardMedia, IconButton } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import { Row, Item } from '@mui-treasury/components/flex';
import { Info, InfoSubtitle, InfoTitle } from '@mui-treasury/components/info';
import { useNewsInfoStyles } from '@mui-treasury/styles/info/news';
import { useCoverCardMediaStyles } from '@mui-treasury/styles/cardMedia/cover';
import { Share, Favorite, Comment } from '@material-ui/icons';
const useStyles = makeStyles(() => ({
    card: {
        position: 'relative',
        boxShadow: '0 8px 24px 0 rgba(0,0,0,0.12)',
        overflow: 'visible',
        transition: '0.4s',
        cursor: 'pointer',
        '&:hover': {
            transform: 'translateY(-2px)',
            '& $shadow': {
                bottom: '-1.5rem',
            },
            '& $shadow2': {
                bottom: '-2.5rem',
            },
        },
        '&:before': {
            content: '""',
            position: 'absolute',
            zIndex: 0,
            display: 'block',
            width: '100%',
            bottom: -1,
            height: '100%',
            backgroundColor: 'rgba(0,0,0,0.08)',
        },
    },
    main: {
        overflow: 'hidden',
        zIndex: 1,
        '&:after': {
            content: '""',
            position: 'absolute',
            bottom: 0,
            display: 'block',
            width: '100%',
            height: '100%',
            background: 'linear-gradient(to top, #014a7d, rgba(0,0,0,0))',
        },
    },
    content: {
        position: 'absolute',
        bottom: 0,
        zIndex: 1,
        padding: '1.5rem 1.5rem 1rem',
    },
    avatar: {
        width: 48,
        height: 48,
    },
    tag: {
        display: 'inline-block',
        fontFamily: "'Sen', sans-serif",
        backgroundColor: '#ff5dac',
        padding: '2px 0.5rem',
        color: '#fff',
        marginBottom: '0.5rem',
    },
    title: {
        fontFamily: "'Sen', sans-serif",
        fontSize: '2rem',
        fontWeight: 800,
        color: '#fff',
    },
    author: {
        zIndex: 1,
        position: 'relative',
    },
    iconButton: {
        minWidth: 10,
        maxWidth: 10, minHeight: 10,
        maxHeight: 10,
    }
}));

export const ArticleCard = React.memo(function News3Card() {
    const styles = useStyles();
    const mediaStyles = useCoverCardMediaStyles();
    return (
        <>
            <NoSsr>
                <GoogleFontLoader fonts={[{ font: 'Sen', weights: [400, 800] }]} />
            </NoSsr>
            <Card className={styles.card}>
                <Box className={styles.main} minHeight={250} position={'relative'}>
                    <CardMedia
                        classes={mediaStyles}
                        image={
                            'https://images.unsplash.com/photo-1507676184212-d03ab07a01bf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2250&q=80'
                        }
                    />
                    <div className={styles.content}>
                        <div className={styles.tag}>Fashion</div>
                        <Typography variant={'h2'} className={styles.title}>
                            Diana Marvel Has a City Take on the Cowboy Boot
            </Typography>
                    </div>
                </Box>
                <Row
                    className={styles.author}
                    m={0}
                    p={3}
                    pt={2}
                    gap={2}
                    bgcolor={'common.white'}
                >
                    <Item>
                        <Avatar
                            className={styles.avatar}
                            src={'https://i.pravatar.cc/300?img=13'}
                        />
                    </Item>
                    <Info position={'middle'} useStyles={useNewsInfoStyles}>
                        <InfoTitle>Nadine Petrolli</InfoTitle>
                        <InfoSubtitle>Jul 20 | 2 Min Read</InfoSubtitle>

                    </Info>
                    <IconButton className={styles.iconButton} size="small" aria-label="settings" color="secondary" disableRipple={true} >
                        <Favorite />
                    </IconButton>
                    <IconButton size="small" aria-label="settings" disableRipple >
                        <Share />
                    </IconButton>
                    <IconButton size="small" aria-label="settings" disableRipple >
                        <Comment />
                    </IconButton>
                </Row>
                <div className={styles.shadow} />
                <div className={`${styles.shadow} ${styles.shadow2}`} />
            </Card>
        </>
    );
});
export default ArticleCard;










