import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import BottomNavigation from '@material-ui/core/BottomNavigation';
// import Toolbar from '@material-ui/core/Toolbar';
// import Typography from '@material-ui/core/Typography';
// import Button from '@material-ui/core/Button';
import Link from '@material-ui/core/Link';
// import IconButton from '@material-ui/core/IconButton';
// import LinkIcon from '@material-ui/icons/Link';
// import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import { Box} from '@material-ui/core';
const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%'
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    footerLink: {
        textDecoration: 'none !important',
        padding: '1%',
        fontSize: '12px'
    }

}));

export default function Header() {
    const classes = useStyles();

    return (
        <div className={classes.root} >

            <Container maxWidth="xl" style={{ backgroundColor: '#0a0a0a', textAlign: 'center' }}>
                <Box component="div" style={{ color: 'white', fontSize: '12px' }}>
                    Copyright ©{new Date().getFullYear()}
                         All rights reserved
                    &nbsp;    |
                    <Link component="button" className={classes.footerLink}
                        variant="body2">About</Link>
                    <Link component="button" className={classes.footerLink}
                        variant="body2"> Contact</Link>
                    <Link component="button" className={classes.footerLink}
                        variant="body2">Disclaimer</Link>
                    <Link component="button" className={classes.footerLink}
                        variant="body2">Privacy</Link>
                </Box>
            </Container >
        </div >
    );
}
